nt-family: var(--documentLetter);\n}\n\nmain {\n    height: fit-content;\n}\n\na {\n    text-decoration: none;\n    color: black;\n}\n\nfigure {\n    margin: 0;\n}\n\n:root {\n    --darkBlue: #68a9c7;\n    --headerBackground: #fed403;\n    --letters: #f7f7f7;\n    --buttonHover: #061922;\n    --flowerPurple: #9561ac;\n    --blueSky:#b6e6fd;\n    --flowerPink: #6ebc37;\n    --borderColor: #8b4507;\n    --grassGreen: #7ec406;\n    --sunRed: #ef5553;\n    --backgroundButtons: #fcff82;\n    --bacgroundHover:#e8630a;\n    --iconsSize: 2.5rem;\n    --generalLetterSize: 1rem;\n    --tittleLetter: 2rem;\n    --infoLetterSixe: 1.3rem;\n    --cardTitle: 1rem; \n    --documentLetter: 'Luckiest Guy', cursive;\n    --paragraphLetters: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;\n}\n\n/*HEADER*/\n\n.main-nav {\n    width: 100%;\n    height: fit-content;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    background: var(--headerBackground);\n}\n\n.nav-figure {\n    width: 30%;\n    height: fit-content;\n    margin: 8px;\n    border-radius: 50%;\n    background: #fcff82;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.nav-figure img {\n    width: 90%;\n    height: 90%;\n}\n\n.menu {\n    width: 100vw;\n    display: grid;\n    grid-template-columns: auto auto;\n    place-content: center;\n}\n\n.nav-item {\n    width: 80%;\n    max-width: 180px;\n    min-width: 130px;\n    height: 68%;\n    display: grid;\n    place-content: center;\n    margin: 5px 10px;\n    background: var(--bacgroundHover);\n    border-radius: 15px;\n}\n\n.ul-container {\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\nul {\n    padding: 0;\n}\n\n.nav-item a {\n    font-size: var(--infoLetterSixe);\n    color: var(--letters);\n}\n\n/*WELCOME SECTION*/\n\n.intro-img-container {\n    width: 100%;\n    height: fit-content;\n    position: relative;\n}\n\n.in